  width: 100%;
  height: 100%;
}

.swiper-slide {
  display: -webkit-box;
  display: -moz-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-button-next, .swiper-button-previous {
  -webkit-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  margin-top: 0px;
}

div[data-carousel=swiper] {
  position: relative;
  margin-bottom: 15px;
}

.swiper-wrapper {
  align-items: center;
}

.swiper-pagination, .swiper-pagination-clickable {
  display: block;
  position: absolute;
  text-align: center;
  transition: 0.3s opacity;
  transform: translate3d(0, 0, 0);
  z-index: 10;
  width: 100%;
}

.swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  text-align: center;
  line-height: 20px;
  font-size: 12px;
  color: #000;
  opacity: 1;
